import { useState, useEffect, useRef } from "react";
import * as d3 from "d3";

const HealthcareMap = () => {
  const [healthLocations, setHealthLocations] = useState([]);
  const [userLocation, setUserLocation] = useState(null);
  const [favorites, setFavorites] = useState([]);
  const [selectedLocation, setSelectedLocation] = useState(null);
  const [transportMode, setTransportMode] = useState("foot");
  const [isRouteShown, setIsRouteShown] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const mapContainerRef = useRef(null);
  const svgRef = useRef(null);
  const mapInitialized = useRef(false);

  // Cargar datos desde el JSON proporcionado
  useEffect(() => {
    // Simulamos la carga de los datos (en un caso real usaras fetch)
    const locationsData = [
      {
        id: 1,
        nombre: "Hospital Universitario de Salamanca",
        tipo: "hospital",
        coordenadas: {
          lat: 40.9634,
          lng: -5.6634,
        },
        direccion: "Paseo de San Vicente, 58, 37007 Salamanca",
        telefono: "923 29 11 00",
        horario: "Lunes a Viernes: 8:00 - 20:00, Sbados: 9:00 - 14:00",
        descripcion:
          "Hospital pblico con servicios de urgencias, consultas externas y especialidades mdicas.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Urgencias", "Consultas", "Radiologa", "Ciruga"],
        },
      },
      {
        id: 2,
        nombre: "Farmacia Calle Zamora",
        tipo: "farmacia",
        coordenadas: {
          lat: 40.9652,
          lng: -5.6643,
        },
        direccion: "Calle Zamora, 12, 37002 Salamanca",
        telefono: "923 12 34 56",
        horario: "Lunes a Domingo: 9:00 - 21:00",
        descripcion:
          "Farmacia con amplio surtido de medicamentos y productos de parafarmacia.",
        accesibilidad: {
          discapacitados: true,
          mascotas: true,
          servicios: ["Venta de medicamentos", "Asesoramiento farmacutico"],
        },
        guardia: "no",
      },
      {
        id: 3,
        nombre: "Urgencias Salamanca",
        tipo: "urgencias",
        coordenadas: {
          lat: 40.9618,
          lng: -5.6672,
        },
        direccion: "Av. de la Aldehuela, s/n, 37007 Salamanca",
        telefono: "923 23 45 67",
        horario: "Abierto 24 horas",
        descripcion: "Servicio de urgencias mdicas disponible las 24 horas.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Atencin mdica urgente", "Traumatologa", "Pediatra"],
        },
      },
      {
        id: 4,
        nombre: "Clnica Salamanca",
        tipo: "clinica",
        coordenadas: {
          lat: 40.9647,
          lng: -5.6589,
        },
        direccion: "Calle Gran Va, 45, 37003 Salamanca",
        telefono: "923 98 76 54",
        horario: "Lunes a Viernes: 9:00 - 19:00",
        descripcion:
          "Clnica privada con servicios de medicina general y especialidades.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Medicina general", "Dermatologa", "Ginecologa"],
        },
      },
      {
        id: 5,
        nombre: "Farmacia Plaza Mayor",
        tipo: "farmacia",
        coordenadas: {
          lat: 40.9649,
          lng: -5.6632,
        },
        direccion: "Plaza Mayor, 10, 37002 Salamanca",
        telefono: "923 21 43 65",
        horario: "Lunes a Domingo: 9:00 - 22:00",
        descripcion:
          "Farmacia ubicada en el corazn de Salamanca, con servicio de entrega a domicilio.",
        accesibilidad: {
          discapacitados: true,
          mascotas: true,
          servicios: ["Venta de medicamentos", "Servicio a domicilio"],
        },
        guardia: "si",
      },
      {
        id: 6,
        nombre: "Centro de Salud Salamanca Centro",
        tipo: "centro_salud",
        coordenadas: {
          lat: 40.9658,
          lng: -5.6615,
        },
        direccion: "Calle Toro, 33, 37002 Salamanca",
        telefono: "923 26 78 90",
        horario: "Lunes a Viernes: 8:00 - 15:00",
        descripcion:
          "Centro de salud pblico con servicios de atencin primaria.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Atencin primaria", "Vacunacin", "Enfermera"],
        },
      },
      {
        id: 7,
        nombre: "Farmacia San Juan",
        tipo: "farmacia",
        coordenadas: {
          lat: 40.9662,
          lng: -5.6598,
        },
        direccion: "Calle San Juan, 8, 37001 Salamanca",
        telefono: "923 25 67 89",
        horario: "Lunes a Sbado: 9:00 - 21:00",
        descripcion:
          "Farmacia con amplia experiencia en el cuidado de la salud.",
        accesibilidad: {
          discapacitados: true,
          mascotas: true,
          servicios: ["Venta de medicamentos", "Asesoramiento nutricional"],
        },
        guardia: "no",
      },
      {
        id: 8,
        nombre: "Clnica Dental Salamanca",
        tipo: "clinica_dental",
        coordenadas: {
          lat: 40.9631,
          lng: -5.6603,
        },
        direccion: "Calle Ruiz Aguilera, 15, 37002 Salamanca",
        telefono: "923 22 33 44",
        horario: "Lunes a Viernes: 10:00 - 18:00",
        descripcion:
          "Clnica dental especializada en ortodoncia y esttica dental.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Ortodoncia", "Blanqueamiento dental", "Implantes"],
        },
      },
      {
        id: 9,
        nombre: "Farmacia La Alamedilla",
        tipo: "farmacia",
        coordenadas: {
          lat: 40.9605,
          lng: -5.6627,
        },
        direccion: "Calle La Alamedilla, 5, 37005 Salamanca",
        telefono: "923 24 56 78",
        horario: "Lunes a Domingo: 9:00 - 21:00",
        descripcion:
          "Farmacia con servicio de atencin personalizada y entrega a domicilio.",
        accesibilidad: {
          discapacitados: true,
          mascotas: true,
          servicios: ["Venta de medicamentos", "Servicio a domicilio"],
        },
        guardia: "si",
      },
      {
        id: 10,
        nombre: "Hospital Clnico de Salamanca",
        tipo: "hospital",
        coordenadas: {
          lat: 40.9623,
          lng: -5.6658,
        },
        direccion: "Paseo de San Vicente, 108, 37007 Salamanca",
        telefono: "923 29 11 11",
        horario: "Lunes a Viernes: 8:00 - 20:00, Sbados: 9:00 - 14:00",
        descripcion:
          "Hospital clnico con servicios de alta especializacin y tecnologa avanzada.",
        accesibilidad: {
          discapacitados: true,
          mascotas: false,
          servicios: ["Ciruga", "Oncologa", "Cardiologa"],
        },
      },
    ];

    setHealthLocations(locationsData);

    // Cargar favoritos del localStorage
    const user = JSON.parse(localStorage.getItem("user"));
    if (user) {
      const userFavorites =
        JSON.parse(localStorage.getItem(`favorites_${user.email}`)) || [];
      setFavorites(userFavorites);
    }

    // Obtener la ubicacin del usuario
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        setUserLocation({ lat: latitude, lng: longitude });
        setIsLoading(false);
      },
      (error) => {
        console.error("Error obteniendo ubicacin:", error);
        setIsLoading(false);
      }
    );
  }, []);

  // Inicializar y gestionar el mapa usando D3
  useEffect(() => {
    if (isLoading || mapInitialized.current || !healthLocations.length) return;

    // Definir dimensiones del mapa
    const width = mapContainerRef.current.clientWidth;
    const height = mapContainerRef.current.clientHeight;

    // Crear SVG
    const svg = d3
      .select(mapContainerRef.current)
      .append("svg")
      .attr("width", width)
      .attr("height", height)
      .attr("viewBox", `0 0 ${width} ${height}`)
      .style("background-color", "#f8f9fa");

    svgRef.current = svg;

    // Crear grupo para el mapa base
    const mapGroup = svg.append("g").attr("class", "map-group");

    // Crear grupo para los marcadores
    const markersGroup = svg.append("g").attr("class", "markers-group");

    // Crear grupo para las rutas
    const routesGroup = svg.append("g").attr("class", "routes-group");

    // Configurar proyeccin geogrfica
    const projection = d3
      .geoMercator()
      .center([-5.6634, 40.9634]) // Centrar en Salamanca
      .scale(450000)
      .translate([width / 2, height / 2]);

    // Dibujar marcadores para los centros de salud
    markersGroup
      .selectAll(".marker")
      .data(healthLocations)
      .enter()
      .append("circle")
      .attr(
        "class",
        (d) =>
          `marker marker-${d.tipo} ${
            favorites.includes(d.id) ? "favorite" : ""
          }`
      )
      .attr("cx", (d) => projection([d.coordenadas.lng, d.coordenadas.lat])[0])
      .attr("cy", (d) => projection([d.coordenadas.lng, d.coordenadas.lat])[1])
      .attr("r", 8)
      .attr("fill", (d) => getMarkerColor(d.tipo))
      .attr("stroke", (d) => (favorites.includes(d.id) ? "#eab308" : "#fff"))
      .attr("stroke-width", (d) => (favorites.includes(d.id) ? 3 : 1))
      .on("click", (event, d) => {
        setSelectedLocation(d);
      });

    // Aadir marcador de ubicacin del usuario si est disponible
    if (userLocation) {
      markersGroup
        .append("circle")
        .attr("class", "user-location")
        .attr("cx", projection([userLocation.lng, userLocation.lat])[0])
        .attr("cy", projection([userLocation.lng, userLocation.lat])[1])
        .attr("r", 8)
        .attr("fill", "#4ade80")
        .attr("stroke", "#fff")
        .attr("stroke-width", 2);
    }

    mapInitialized.current = true;

    // Funcin de limpieza
    return () => {
      if (svgRef.current) {
        svgRef.current.remove();
      }
    };
  }, [isLoading, healthLocations, favorites, userLocation]);

  // Actualizar la ruta cuando cambia el centro seleccionado o el modo de transporte
  useEffect(() => {
    if (!selectedLocation || !userLocation || !svgRef.current || !isRouteShown)
      return;

    // Limpiar rutas existentes
    svgRef.current.select(".routes-group").selectAll("*").remove();

    const routesGroup = svgRef.current.select(".routes-group");
    const projection = d3
      .geoMercator()
      .center([-5.6634, 40.9634])
      .scale(450000)
      .translate([
        mapContainerRef.current.clientWidth / 2,
        mapContainerRef.current.clientHeight / 2,
      ]);

    // Simulamos una ruta directa (en un caso real se usara una API de enrutamiento)
    const routePoints = [
      [userLocation.lng, userLocation.lat],
      [selectedLocation.coordenadas.lng, selectedLocation.coordenadas.lat],
    ];

    // Dibujar la lnea de la ruta
    const lineGenerator = d3
      .line()
      .x((d) => projection(d)[0])
      .y((d) => projection(d)[1])
      .curve(d3.curveBasis);

    routesGroup
      .append("path")
      .datum(routePoints)
      .attr("class", "route")
      .attr("d", lineGenerator)
      .attr("stroke", getRouteColor(transportMode))
      .attr("stroke-width", 4)
      .attr("fill", "none")
      .attr("stroke-dasharray", transportMode === "foot" ? "5,5" : "none");
  }, [selectedLocation, userLocation, transportMode, isRouteShown]);

  // Funcin para alternar favoritos
  const toggleFavorite = (id) => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (!user) {
      alert("Debes iniciar sesin para guardar favoritos");
      return;
    }

    let newFavorites;
    if (favorites.includes(id)) {
      newFavorites = favorites.filter((favId) => favId !== id);
    } else {
      newFavorites = [...favorites, id];
    }

    // Guardar en localStorage
    localStorage.setItem(
      `favorites_${user.email}`,
      JSON.stringify(newFavorites)
    );
    setFavorites(newFavorites);

    // Actualizar el estilo de los marcadores
    if (svgRef.current) {
      svgRef.current
        .selectAll(".marker")
        .attr("stroke", (d) =>
          newFavorites.includes(d.id) ? "#eab308" : "#fff"
        )
        .attr("stroke-width", (d) => (newFavorites.includes(d.id) ? 3 : 1));
    }
  };

  // Funcin para calcular rutas
  const calculateRoute = () => {
    if (!selectedLocation || !userLocation) {
      alert("Selecciona un centro de salud primero");
      return;
    }

    setIsRouteShown(true);
  };

  // Funcin para obtener el color segn el tipo de centro
  const getMarkerColor = (tipo) => {
    switch (tipo) {
      case "hospital":
        return "#dc2626"; // Rojo
      case "farmacia":
        return "#2563eb"; // Azul
      case "urgencias":
        return "#9333ea"; // Morado
      case "clinica":
        return "#ca8a04"; // mbar
      case "centro_salud":
        return "#16a34a"; // Verde
      case "clinica_dental":
        return "#0891b2"; // Cian
      default:
        return "#6b7280"; // Gris
    }
  };

  // Funcin para obtener el color de ruta segn el modo de transporte
  const getRouteColor = (mode) => {
    switch (mode) {
      case "foot":
        return "#2563eb"; // Azul
      case "bike":
        return "#16a34a"; // Verde
      case "car":
        return "#dc2626"; // Rojo
      default:
        return "#6b7280"; // Gris
    }
  };

  if (isLoading) {
    return (
      <div className="w-full h-screen flex items-center justify-center bg-gray-100">
        <div className="text-gray-800 text-xl">Cargando mapa...</div>
      </div>
    );
  }

  return (
    <div className="w-full h-screen relative bg-gray-100">
      {/* Contenedor del mapa */}
      <div
        ref={mapContainerRef}
        className="w-full h-full"
        style={{ height: "calc(100vh - 64px)" }}
      ></div>

      {/* Controles del mapa */}
      <div className="absolute top-4 right-4 bg-white p-4 rounded-lg shadow-lg z-10 border border-gray-200">
        <h3 className="font-bold mb-2 text-gray-800">Modo de transporte:</h3>
        <select
          value={transportMode}
          onChange={(e) => setTransportMode(e.target.value)}
          className="p-2 border border-gray-300 rounded bg-white text-gray-800 w-full"
        >
          <option value="foot">A pie</option>
          <option value="bike">Bicicleta</option>
          <option value="car">Coche</option>
        </select>
      </div>

      {/* Panel de informacin */}
      {selectedLocation && (
        <div className="absolute bottom-4 left-4 bg-white p-4 rounded-lg shadow-lg z-10 border border-gray-200 max-w-md">
          <h2 className="font-bold text-xl text-gray-800">
            {selectedLocation.nombre}
          </h2>
          <p className="text-gray-600 mt-1">{selectedLocation.direccion}</p>
          <p className="text-gray-600">Telfono: {selectedLocation.telefono}</p>
          <p className="text-gray-600">Horario: {selectedLocation.horario}</p>
          <div className="mt-3 flex space-x-2">
            <button
              onClick={() => toggleFavorite(selectedLocation.id)}
              className={`px-3 py-1 rounded flex items-center ${
                favorites.includes(selectedLocation.id)
                  ? "bg-yellow-100 text-yellow-600"
                  : "bg-gray-100 text-gray-600"
              }`}
            >
              <span className="mr-1">
                {favorites.includes(selectedLocation.id) ? "" : ""}
              </span>
              {favorites.includes(selectedLocation.id)
                ? "Favorito"
                : "Aadir a favoritos"}
            </button>
            <button
              onClick={calculateRoute}
              className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
            >
              {isRouteShown ? "Actualizar ruta" : "Calcular ruta"}
            </button>
          </div>

          {/* Servicios disponibles */}
          {selectedLocation.accesibilidad.servicios.length > 0 && (
            <div className="mt-3">
              <h3 className="font-semibold text-gray-700">Servicios:</h3>
              <ul className="mt-1 text-gray-600 pl-5 list-disc">
                {selectedLocation.accesibilidad.servicios.map(
                  (servicio, index) => (
                    <li key={index}>{servicio}</li>
                  )
                )}
              </ul>
            </div>
          )}

          {/* Accesibilidad */}
          <div className="mt-3 flex space-x-4">
            <div className="flex items-center">
              <span
                className={`w-3 h-3 rounded-full ${
                  selectedLocation.accesibilidad.discapacitados
                    ? "bg-green-500"
                    : "bg-red-500"
                } mr-1`}
              ></span>
              <span className="text-sm text-gray-600">Accesible</span>
            </div>
            <div className="flex items-center">
              <span
                className={`w-3 h-3 rounded-full ${
                  selectedLocation.accesibilidad.mascotas
                    ? "bg-green-500"
                    : "bg-red-500"
                } mr-1`}
              ></span>
              <span className="text-sm text-gray-600">Mascotas</span>
            </div>
            {selectedLocation.tipo === "farmacia" &&
              selectedLocation.guardia && (
                <div className="flex items-center">
                  <span
                    className={`w-3 h-3 rounded-full ${
                      selectedLocation.guardia === "si"
                        ? "bg-green-500"
                        : "bg-gray-300"
                    } mr-1`}
                  ></span>
                  <span className="text-sm text-gray-600">Guardia</span>
                </div>
              )}
          </div>
        </div>
      )}

      {/* Leyenda */}
      <div className="absolute bottom-4 right-4 bg-white p-3 rounded-lg shadow-lg z-10 border border-gray-200">
        <h3 className="font-bold mb-2 text-gray-800 text-sm">Leyenda:</h3>
        <div className="grid grid-cols-2 gap-2">
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-red-600 mr-1"></span>
            <span className="text-xs text-gray-600">Hospital</span>
          </div>
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-blue-600 mr-1"></span>
            <span className="text-xs text-gray-600">Farmacia</span>
          </div>
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-purple-600 mr-1"></span>
            <span className="text-xs text-gray-600">Urgencias</span>
          </div>
          <div className="flex items-center">
            <span className="w-3 h-3 rounded-full bg-green-600 mr-1"></span>
            <span className="text-xs text-gray-600">Centro Salud</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HealthcareMap;
